@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loaderr {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99999;
  background-color: #185090;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loaderr .loader {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 5px solid #EC8123;
  border-left: 5px solid #185090;
  animation: spin 1s linear infinite;
}

.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(24, 80, 144, 0.8);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}

.navover.open {
  opacity: 1;
  visibility: visible;
}

.sideNav {
  padding: 24px;
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: #185090;
  z-index: 9999;
  overflow-x: hidden;
  overflow-y: hidden;
  transition: 0.5s;
  transform: scale(0);
  transform-origin: top right;
}

html[dir="ltr"] .sideNav {
  transform-origin: top left;
}

.sideNav.open {
  transform: scale(1);
  transform-origin: top left;
}

html[dir="ltr"] .sideNav.open {
  transform-origin: top right;
}

.sideNav .logo img {
  filter: brightness(0);
  opacity: .1;
  transform: scale(2.1);
  position: relative;
  z-index: -1;
}

.sideNav .opt {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}

.sideNav .opt .lang a {
  color: #FFF;
  font-size: 15px;
}

.sideNav .opt .close1 {
  font-size: 20px;
  color: #FFF;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.sideNav .links {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.sideNav .links > ul {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  overflow: auto;
  text-align: center;
}

.sideNav .links > ul > li {
  padding: 7px 15px;
  width: 100%;
}

.sideNav .links > ul > li a {
  width: 100%;
  display: block;
  font-size: 20px;
  color: #FFF;
  transition: 0.5s;
}

.sideNav .links > ul > li a.lnr {
  font-family: "tj";
  position: relative;
  width: 125px;
}

html[dir="ltr"] .sideNav .links > ul > li a.lnr {
  margin: auto;
}

.sideNav .links > ul > li a.lnr:before {
  position: absolute;
  top: 50%;
  left: 23px;
  transform: translateY(-15%);
  font-family: "Linearicons-Free" !important;
  color: #185090;
  font-size: 8px;
  transition: 0.5s;
}

html[dir="ltr"] .sideNav .links > ul > li a.lnr:before {
  right: 20px;
  left: unset;
}

.sideNav .links > ul > li a.lnr + .dropDown {
  display: none;
  padding-top: 10px;
}

.sideNav .links > ul > li a.lnr + .dropDown li:not(:last-child) {
  margin-bottom: 10px;
}

.sideNav .links > ul > li a.lnr + .dropDown li a {
  color: #898989;
}

.searchh {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}

.searchh > img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  opacity: 0.08;
  z-index: -1;
  transition: 0.5s 0.5s;
}

.searchh .inner {
  position: relative;
  width: 50%;
  border: 2px solid #175092;
  z-index: 9;
  transform: scale(1.4);
  opacity: 0;
  visibility: hidden;
  transition: 0.5s 0.5s;
  border-radius: 100px;
}

@media (max-width: 767px) {
  .searchh .inner {
    width: 90%;
    transform: scale(1.4) translateY(-115px);
  }
}

.searchh .inner > span {
  position: absolute;
  top: -120px;
  left: 50%;
  transform: translateX(-50%);
  color: #fff;
  font-size: 25px;
}

@media (max-width: 767px) {
  .searchh .inner > span {
    top: -90px;
  }
}

.searchh .inner input {
  width: 100%;
  height: 50px;
  border: 0;
  padding: 0 25px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 100px;
}

.searchh .inner button {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 60px;
  background-color: #175092;
  border: 0;
  color: #fff;
  font-size: 25px;
  cursor: pointer;
  transition: 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px 0 0 20px;
}

html[dir="ltr"] .searchh .inner button {
  right: 0;
  left: unset;
}

html[dir="ltr"] .searchh .inner button {
  border-radius: 0 20px 20px 0;
}

.searchh .inner button:hover {
  background-color: #ec8123;
}

.searchh.open {
  opacity: 1;
  visibility: visible;
}

.searchh.open .inner {
  opacity: 1;
  visibility: visible;
  transform: scale(1) translateY(-115px);
}

.searchh.open:after {
  height: 100%;
}

.searchh.open:before {
  width: 100%;
}

.searchh.open img {
  transform: translate(-50%, -50%) rotate(0deg);
}

