@import "main";
// End Of Import

// Preloader

.loaderr{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    background-color: $color-sec;
    @include Flex;

    .loader{
        width: 80px;
        height: 80px;
        border-radius: 50%;
        border: 5px solid #EC8123;
        border-left: 5px solid $color-sec;
        animation: spin 1s linear infinite;
    }
}

// Preloader

.navover {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($color-sec, 0.8);
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    padding: 24px;
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: $color-sec;
    z-index: 9999;
    overflow-x: hidden;
    overflow-y: hidden;
    transition: 0.5s;
    transform: scale(0);
    transform-origin: top right;
    
    html[dir="ltr"] & {
        transform-origin: top left;
    }

    &.open {
        transform: scale(1);
        transform-origin: top left;
        
        html[dir="ltr"] & {
            transform-origin: top right;
        }
    }

    .logo{

        img{
            filter: brightness(0);
            opacity: .1;
            transform: scale(2.1);
            position: relative;
            z-index: -1;
        }
    }

    .opt {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        .lang {
            a {
                color: $color-white;
                font-size: 15px;
            }
        }

        .close1 {
            font-size: 20px;
            color: $color-white;
            cursor: pointer;
            display: flex;
            align-items: center;
        }
    }

    .links {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        > ul {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            height: 100%;
            overflow: auto;
            text-align: center;

            & > li {
                padding: 7px 15px;
                width: 100%;

                a {
                    width: 100%;
                    display: block;
                    font-size: 20px;
                    color: $color-white;
                    // line-height: 1.6;
                    transition: 0.5s;

                    &.lnr {
                        font-family: "tj";
                        position: relative;
                        width: 125px;
                        html[dir="ltr"] & {
                            margin: auto;
                            // width: 170px;
                        }
                        &:before {
                            position: absolute;
                            top: 50%;
                            left: 23px;
                            transform: translateY(-15%);
                            font-family: "Linearicons-Free" !important;
                            color: $color-sec;
                            font-size: 8px;
                            transition: 0.5s;
                            html[dir="ltr"] & {
                                right: 20px;
                                left: unset;
                            }
                        }

                        + .dropDown {
                            display: none;
                            padding-top: 10px;

                            li {
                                &:not(:last-child) {
                                    margin-bottom: 10px;
                                }

                                a {
                                    color: #898989;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

// Side Nav

// Search

.searchh {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.95);
    @include Flex;
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

    > img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-45deg);
        opacity: 0.08;
        z-index: -1;
        transition: 0.5s 0.5s;
    }

    // &:after {
    //     content: "";
    //     width: 100%;
    //     height: 30%;
    //     position: absolute;
    //     top: 50%;
    //     transform: translateY(-50%);
    //     left: 0;
    //     border: 3px solid $color-prim;
    //     border-right: 0;
    //     border-left: 0;
    //     transition: 0.5s 0.5s;
    // }

    // &:before {
    //     content: "";
    //     width: 30%;
    //     height: 100%;
    //     position: absolute;
    //     left: 50%;
    //     transform: translateX(-50%);
    //     top: 0;
    //     border: 3px solid $color-prim;
    //     border-top: 0;
    //     border-bottom: 0;
    //     transition: 0.5s 0.5s;
    // }

    .inner {
        position: relative;
        width: 50%;
        border: 2px solid $color-prim;
        z-index: 9;
        transform: scale(1.4);
        opacity: 0;
        visibility: hidden;
        transition: 0.5s 0.5s;
        border-radius: 100px;

        @include Xs {
            width: 90%;
            transform: scale(1.4) translateY(-115px);
        }

        > span {
            position: absolute;
            top: -120px;
            left: 50%;
            transform: translateX(-50%);
            color: #fff;
            font-size: 25px;

            @include Xs {
                top: -90px;
            }
        }

        input {
            width: 100%;
            height: 50px;
            border: 0;
            padding: 0 25px;
            background-color: rgba(255, 255, 255, 0.9);
            border-radius: 100px;
        }

        button {
            position: absolute;
            top: 0;
            left: 0;
            html[dir="ltr"] & {
                right: 0;
                left: unset;
            }
            height: 100%;
            width: 60px;
            background-color: $color-prim;
            border: 0;
            color: #fff;
            font-size: 25px;
            cursor: pointer;
            transition: 0.5s;
            @include Flex;
            border-radius: 20px 0 0 20px;
            @include LTR {
                border-radius: 0 20px 20px 0;
            }

            &:hover {
                background-color: #ec8123;
            }
        }
    }

    &.open {
        opacity: 1;
        visibility: visible;

        .inner {
            opacity: 1;
            visibility: visible;
            transform: scale(1) translateY(-115px);
        }

        &:after {
            height: 100%;
        }

        &:before {
            width: 100%;
        }

        img{
            transform: translate(-50%, -50%) rotate(0deg);
        }
    }
}

