// Start Header

.header {
    padding: 17px 0;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);

    .content {
        display: flex;
        align-items: center;

        @include Sm {
            justify-content: space-between;
        }

        @include Xs {
            flex-direction: column;
        }

        .links {
            @include Md {
                margin-left: 40px;
                margin-right: auto;
                @include LTR {
                    margin-right: 40px;
                    margin-left: auto;
                }
            }

            @include Lg {
                margin-left: 70px;
                margin-right: auto;
                @include LTR {
                    margin-right: 150px;
                    margin-left: auto;
                }
            }

            @include Sm {
                display: none;
            }

            ul {
                display: flex;

                li {
                    position: relative;
                    display: flex;
                    align-items: center;
                    &:not(:last-child) {
                        @include Md {
                            margin-left: 20px;

                            @include LTR {
                                margin-right: 20px;
                                margin-left: 0;
                            }
                        }

                        @include Lg {
                            margin-left: 25px;

                            @include LTR {
                                margin-right: 25px;
                                margin-left: 0;
                            }
                        }
                    }

                    a {
                        font-family: "tjBold";
                        font-size: 16px;
                        color: $color-prim;
                        transition: 0.5s;

                        &.lnr {
                            margin-left: 9px;
                            &:before {
                                position: absolute;
                                top: 50%;
                                left: -5px;
                                transform: translateY(-50%);
                                font-family: "Linearicons-Free" !important;
                                color: currentColor;
                                font-size: 9px;
                                font-weight: bold;
                                transition: 0.5s;
                                @include LTR {
                                    right: -5px;
                                    left: unset;
                                }
                            }

                            @include LTR {
                                margin-right: 9px;
                                margin-left: 0;
                            }
                        }

                        @include Md {
                            font-size: 15px;
                        }

                        @include Lg {
                            font-size: 16px;
                        }

                        &:hover {
                            color: #ec8123;
                        }
                    }

                    .dropDown {
                        position: absolute;
                        right: 50%;
                        top: 140%;
                        width: 160px;
                        background-color: $color-white;
                        padding: 7px 10px;
                        box-shadow: 0 0 20px rgba($color-prim, 0.1);
                        opacity: 0;
                        border-radius: 10px;
                        visibility: hidden;
                        z-index: 9;
                        transform: translateY(-10px) translateX(50%);
                        transition: 0.5s;

                        // @include LTR {
                        //   left: 50%;
                        //   right: unset;
                        // }

                        &:after {
                            content: "";
                            width: 0;
                            height: 0;
                            display: block;
                            border-width: 6px;
                            border-color: transparent transparent $color-white;
                            border-style: solid;
                            bottom: 100%;
                            position: absolute;
                            right: 50%;
                            transform: translateX(50%);
                        }

                        ul {
                            display: flex;
                            flex-direction: column;
                        }

                        li {
                            margin: 0 !important;

                            &:not(:last-child) {
                                border-bottom: 1px solid #fafafa;
                            }

                            a {
                                color: $color-prim;
                                line-height: 2.4;
                                font-size: 15px;
                                font-family: "tj";
                                text-align: center;
                                display: block;
                                width: 100%;
                                transition: 0.5s;

                                html[dir="ltr"] & {
                                    font-size: 14px;
                                }

                                &:after {
                                    display: none;
                                }

                                &:hover {
                                    color: #ec8123;

                                    &:after {
                                        color: currentColor;
                                    }
                                }
                            }
                        }
                    }

                    &:hover {
                        .dropDown {
                            transform: translateY(0) translateX(50%);
                            opacity: 1;
                            visibility: visible;
                        }
                    }
                }
            }
        }

        .options {
            display: flex;
            align-items: center;

            @include Xs {
                margin-top: 30px;
            }

            > div {
                display: flex;
                align-items: center;
                cursor: pointer;
                transition: 0.5s;

                &.menuTriger {
                    display: none;

                    @include Sm {
                        display: flex;
                    }
                }

                &:not(:last-child) {
                    margin-left: 20px;
                    @include LTR {
                        margin-right: 20px;
                        margin-left: 0;
                    }
                }

                &.user {
                    position: relative;
                    margin-left: 15px;
                    @include LTR {
                        margin-right: 15px;
                        margin-left: 0;
                    }

                    span.lnr-chevron-down {
                        font-size: 8px;
                        margin-left: 0;
                        margin-right: 5px;
                        @include LTR {
                            margin-right: 0;
                            margin-left: 5px;
                        }
                    }

                    .dropDown {
                        position: absolute;
                        right: 50%;
                        top: 140%;
                        width: 160px;
                        background-color: $color-white;
                        padding: 7px 10px;
                        box-shadow: 0 0 20px rgba($color-prim, 0.1);
                        opacity: 0;
                        border-radius: 10px;
                        visibility: hidden;
                        z-index: 9;
                        transform: translateY(-10px) translateX(50%);
                        transition: 0.5s;

                        // @include LTR {
                        //   left: 50%;
                        //   right: unset;
                        // }

                        @include mob480 {
                            transform: translateY(-10px) translateX(51%);
                            
                            @include LTR {
                                transform: translateY(-10px) translateX(48%);
                            }
                        }

                        &:after {
                            content: "";
                            width: 0;
                            height: 0;
                            display: block;
                            border-width: 6px;
                            border-color: transparent transparent $color-white;
                            border-style: solid;
                            bottom: 100%;
                            position: absolute;
                            right: 50%;
                            transform: translateX(50%);
                        }

                        ul {
                            display: flex;
                            flex-direction: column;
                        }

                        li {
                            margin: 0 !important;

                            &:not(:last-child) {
                                border-bottom: 1px solid #fafafa;
                            }

                            a {
                                color: $color-prim;
                                line-height: 2.4;
                                font-size: 15px;
                                font-family: "tj";
                                text-align: center;
                                display: block;
                                width: 100%;
                                transition: 0.5s;

                                html[dir="ltr"] & {
                                    font-size: 14px;
                                }

                                &:after {
                                    display: none;
                                }

                                &:hover {
                                    color: #ec8123;
                                }
                            }
                        }
                    }

                    &:hover {
                        .dropDown {
                            transform: translateY(0) translateX(50%);
                            opacity: 1;
                            visibility: visible;

                            @include mob480 {
                                transform: translateY(0) translateX(51%);

                                @include LTR {
                                    transform: translateY(0) translateX(48%);
                                }
                            }
                        }
                    }
                }

                &.myCart {
                    position: relative;

                    @include mob600 {
                        margin: 0;
                    }

                    .counTer {
                        position: absolute;
                        top: -5px;
                        right: -5px;
                        @include LTR {
                            right: unset;
                            left: -5px;
                        }
                        @include Flex;
                        width: 12px;
                        height: 12px;
                        background-color: #ec8123;
                        font-family: "tjMd";
                        font-size: 10px;
                        color: #fff;
                        border-radius: 50%;
                    }
                }

                &.lang {
                    @include mob600 {
                        display: none;
                    }
                    .btn-group {
                        .dropdown-menu {
                            box-shadow: unset;
                            width: 100%;
                            border: 1px solid $color-prim;

                            li {
                                a {
                                    transition: 0.5s;

                                    &:hover {
                                        color: #ec8123;
                                        background-color: transparent;
                                    }
                                }
                            }
                        }
                        button {
                            display: flex;
                            align-items: center;
                            border: 0;
                            color: #939393;
                            background-color: transparent !important;
                            box-shadow: none;
                            padding: 0;

                            &:hover,
                            &:active,
                            &:focus {
                                background-color: transparent !important;
                                box-shadow: none;
                            }

                            &:hover {
                                color: #ec8123;
                                span {
                                    color: #ec8123;
                                }
                            }

                            &.btn-default:active:focus {
                                color: #939393;
                            }

                            img {
                                margin-left: 10px;
                                html[dir="ltr"] & {
                                    margin-left: 0;
                                    margin-right: 10px;
                                }
                            }

                            span {
                                margin-right: 10px;
                                html[dir="ltr"] & {
                                    margin-right: 0;
                                    margin-left: 10px;
                                }
                                font-size: 8px;
                                color: #939393;
                            }
                        }
                        &.open .dropdown-toggle,
                        &:active,
                        &:focus {
                            box-shadow: none;
                            color: #939393 !important;
                        }

                        .dropdown-menu {
                            @include Md {
                                transform: translateX(30px);
                                html[dir="ltr"] & {
                                    transform: translateX(-30px);
                                }
                            }

                            @include Lg {
                                transform: translateX(0px);
                            }
                            li {
                                a {
                                    display: flex;
                                    align-items: center;
                                    color: #939393;

                                    html[dir="ltr"] & {
                                        justify-content: flex-start;
                                        direction: rtl;
                                    }

                                    img {
                                        margin-left: 10px;
                                        // html[dir="ltr"] & {
                                        //   margin-left: 0;
                                        //   margin-right: 10px;
                                        // }
                                    }
                                }
                            }
                        }
                    }
                }

                &:hover {
                    *:not(.counTer) {
                        color: #ec8123;
                    }
                }
            }

            span {
                font-size: 14px;
                color: $color-prim;
                &.lnr {
                    font-size: 18px;
                    color: $color-prim;
                    margin-left: 5px;
                    @include LTR {
                        margin-right: 5px;
                        margin-left: 0;
                    }
                }

                &.lnr-chevron-down {
                    font-size: 9px;
                    font-weight: bold;

                    @include mob480 {
                        display: none;
                    }
                }

                @include mob480 {
                    &.text {
                        display: none;
                    }
                }
            }
        }
    }
}

// End Header

.owl-nav {
    position: absolute;
    top: 49%;
    transform: translate(50%, -50%);
    right: 50%;
    width: 100%;
    height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include Sm {
        display: none !important;
    }
}

// Slider Styles

.owl-dots {
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include Xs {
        bottom: 15px;
    }

    button {
        &:not(:last-child) {
            margin-left: 5px;
        }

        &.active {
            span {
                background-color: rgba($color-sec, 0.6);
            }
        }

        span {
            display: block;
            width: 10px;
            height: 10px;
            background-color: rgba($color-sec, 0.3);
        }
    }
}

// Main Slider

.main-slider {
    .owl-nav {
        position: absolute;
        top: 49%;
        transform: translate(50%, -50%);
        right: 50%;
        width: 100%;
        height: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 67px;

        @include Sm {
            display: none;
        }

        button {
            width: 48px;
            height: 48px;
            border-radius: 50%;
            background-color: rgba(0, 0, 0, 0.5) !important;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: 0.5s;

            span {
                font-size: 16px;
                color: #fff;
                transition: 0.5s;
            }

            &:hover {
                background-color: #fff !important;

                span {
                    color: #333;
                }
            }
        }
    }

    .owl-dots {
        position: absolute;
        bottom: 40px;
        right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        display: flex;

        @include mob600 {
            bottom: 10px;
        }

        button {
            &:not(:last-child) {
                margin-left: 5px;
            }

            &.active {
                span {
                    background-color: $color-white;
                }
            }

            span {
                display: block;
                width: 9px;
                height: 9px;
                background-color: rgba($color-black, 0.55);
                border-radius: 50%;
            }
        }
    }
}

// Main Slider

// Categories

