        overflow: hidden;
        margin-top: 32px;
        transition: 0.5s;
        @include Flex;

        img {
            transition: 0.5s;
        }

        &:hover {
            transform: translateY(-10px);
            // img{
            //     transform: scale(1.05) rotate(1deg);
            // }
        }
