// Categories

// Products

.products {
    padding: 80px 0;
    text-align: center;

    @include Xs {
        padding: 40px 0;
    }

    .col-xs-4 {
        @include mob600 {
            width: 50%;
        }

        @include mob480 {
            width: 100%;
        }
    }

    h2 {
        margin: 0;
        margin-bottom: 20px;
        font-family: "tjMd";
        font-size: 24px;
        color: $color-prim;

        + img {
            margin: auto;
            margin-bottom: 30px;
        }
    }
    .pic {
        // width: 266px;
        height: 266px;
        @include Flex;
        margin-bottom: 23px;

        img {
            transition: 2s;
        }
    }

    .title {
        font-size: 15px;
        color: $color-prim;
        margin-bottom: 10px;
    }

    .price {
        font-size: 18px;
        font-family: "tjBold";
        color: #9f1b1f;
    }

    .product {
        overflow: hidden;
        &:hover {
            img {
                transform: translateY(-10px) scale(1.03);
            }
        }

        @include mob480 {
            display: block;
            margin-bottom: 20px;
        }
    }

    .btn10 {
        width: 152px;
        height: 40px;
        color: #939292;
        position: relative;
        font-size: 15px;
        border: 2px solid #ececec;
        border-radius: 100px;
        position: relative;
        margin: auto;
        margin-top: 70px;
        overflow: hidden !important;
        transition: all 0.3s ease-in-out;
        background: transparent !important;
        @include Flex;
        z-index: 10;

        @include mob480 {
            margin-top: 30px;
        }

        &:hover {
            border: 2px solid $color-prim;
            color: #fff !important;
        }

        &:before {
            content: "";
            width: 0%;
            height: 100%;
            display: block;
            background: $color-prim;
            position: absolute;
            transform: skewX(-20deg);
            left: -10%;
            opacity: 1;
            top: 0;
            z-index: -12;
            transition: all 0.7s cubic-bezier(0.77, 0, 0.175, 1);
            box-shadow: 2px 0px 14px rgba(0, 0, 0, 0.6);
        }

        &:after {
            content: "";
            width: 0%;
            height: 100%;
            display: block;
            background: #ed8220;
            position: absolute;
            transform: skewX(-20deg);
            left: -10%;
            opacity: 0;
            top: 0;
            z-index: -15;
            transition: all 0.4s cubic-bezier(0.2, 0.95, 0.57, 0.99);
            box-shadow: 2px 0px 14px rgba(0, 0, 0, 0.6);
        }

        &:hover {
            &:before,
            &:after {
                opacity: 1;
                width: 116%;
            }
        }
    }
}
// Products

// Banner

.banner {
    a {
        position: relative;
        display: block;
        overflow: hidden;
        transition: 0.5s;

        &:after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            transition: 0.5s;
        }

        img {
            transition: 0.5s;
        }

        &:hover {
            &:after {
                box-shadow: inset 0px 0px 30px rgba(#fff, 0.9);
            }
            img {
                transform: scale(1.02);
            }
        }
    }
}

// Banner

// Features

.features {
    padding: 40px 0;
    background-color: #144985;

    .col-md-3 {
        html[dir="ltr"] & {
            padding: 0 3px;
        }
    }

    .feat {
        @include Flex;

        span {
            &.lnr {
                font-size: 24px;
                color: #ed8220;
                margin-left: 10px;
                html[dir="ltr"] & {
                    margin-left: 0;
                    margin-right: 10px;
                }
            }

            &:last-child {
                font-size: 15.7px;
                color: #fff;

                // @include mob600{
                //   width: 28%;
                // }

                // @include mob480{
                //   width: 35.5%;
                // }

                // @include mob375{
                //   width: 46.1%;
                // }

                // @include mob360{
                //   width: 48.2%;
                // }
            }
        }

        @include Sm {
            margin-top: 20px;
            flex-direction: column;

            span {
                margin: 0;
                &.lnr {
                    margin: 0;
                    margin-bottom: 10px;
                }
            }
        }
    }
}

// Features

// footer

footer {
    padding: 65px 0 70px;
    background-color: #185090;

    @include Sm {
        padding: 40px 0;
    }

    .foot-links {
        // @include Lg {
        //   text-align: right;
        // }

        @include Sm {
            margin-bottom: 15px;
        }

        .collapse {
            display: block;

            @include Sm {
                display: none;
            }

            &.in {
                display: block;
            }
        }

        & > button {
            font-size: 16px;
            font-family: "tjBold";
            color: #fff;
            margin-bottom: 15px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans {
                &:after {
                    content: "\2212";
                }
            }

            @include Sm {
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480 {
                width: 100%;
            }

            &:after {
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm {
                    display: block;
                }
            }
        }

        ul {
            @include Sm {
                padding-top: 10px;
                padding-right: 10px;
            }

            li {
                &:not(:last-child) {
                    margin-bottom: 10px;
                }
            }
        }

        a {
            font-size: 14px;
            font-family: "tj";
            color: #c0d9f7;
            transition: 0.5s;

            &:hover {
                color: $color-white;
            }
        }
    }

    .newsletter {
        @include Md {
            margin-top: 35px;
        }

        @include Lg {
            margin-top: 0;
        }

        @include Sm {
            margin-top: 35px;
            text-align: center;
        }

        & > p {
            font-size: 16px;
            font-family: "tjBold";
            color: #fff;
            margin-bottom: 20px;
        }

        & > span {
            font-size: 14px;
            font-family: "tj";
            color: #fff;
            margin-bottom: 15px;
            display: block;
        }
    }

    .frm {
        position: relative;
        margin-bottom: 17px;
        width: 360px;

        @include Md {
            margin: auto;
            margin-bottom: 17px;
        }

        @include Sm {
            margin: auto;
            margin-bottom: 17px;
        }

        @include mob375 {
            width: 280px;
        }

        input {
            padding: 0 20px;
            height: 45px;
            width: 100%;
            border: 0;
            color: #bbbbbb;
            border-radius: 100px;
            font-size: 14px;
            background-color: #fff;
            transition: 0.5s;

            &::placeholder {
                color: #bbbbbb;
            }
        }

        button {
            position: absolute;
            top: 50%;
            left: 6px;
            html[dir="ltr"] & {
                left: unset;
                right: 6px;
            }
            transform: translateY(-50%);
            width: 35px;
            height: 35px;
            border-radius: 50%;
            background-color: #ec8123;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: 0.5s;

            span {
                font-size: 14px;
                color: #fff;
            }

            &:hover {
                background-color: #185090;
            }
        }
    }

    .social {
        @include Flex;

        a {
            @include Flex;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-color: rgba($color-white, 0.1);
            font-size: 18px;
            transition: 0.5s;

            &:not(:last-child) {
                margin-left: 5px;

                html[dir="ltr"] & {
                    margin-left: 0;
                    margin-right: 5px;
                }
            }

            i {
                color: #fff;
            }

            &:hover {
                background-color: #fff;
                i {
                    color: #185090;
                }
            }
        }
    }
}

// End Of Footer

// End

.end {
    background-color: #185090;

    .container {
        border-top: 1px solid #3a69a0;
        padding: 0;
    }

    .content {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include mob600 {
            flex-direction: column-reverse;
        }

        @include Xs {
            text-align: center;
        }

        p,
        a {
            font-size: 14px;
            color: #fff;
            margin-bottom: 0;

            @include Xs {
                font-size: 13px;
            }

            @include mob600 {
                margin-bottom: 10px;
            }
        }

        p {
            @include mob480 {
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                margin-bottom: 0;
            }
        }

        a {
            img {
                backface-visibility: hidden;
                transform: translateZ(0);
                margin-right: 5px;
                html[dir="ltr"] & {
                    margin-right: 0;
                    margin-left: 5px;
                }
            }

            @include mob480 {
                margin-top: 10px;
            }
        }

        > img {
            width: 220px;
            // height: 44px;
            backface-visibility: hidden;
            transform: translateZ(0);
        }
    }
}

// End
